import { styled } from 'styled-components';
import './App.css';
import './common.css';
import { Component } from 'react';

class CreditNotify extends Component{
    render(props){
        return <Container id="Main">
          <Content>
          <TemplateDisplay>
            <div></div>
          </TemplateDisplay>
          </Content>
        </Container>;
    }
}

export default CreditNotify;

const Container = styled.div`
  scroll-margin-top: var(--nav-height);
`;

const Category = styled.div`
  line-height: var(--category-line-height);
  font-size: var(--category-font);
  font-weight: bold;
`;

const Content = styled.div`

`;

const TemplateDisplay = styled.div`
  background-color: gray;
  padding: var(--display-padding);
  margin-bottom: -10rem;
  min-height: 5rem;
`;
