const Container = styled.div`
  scroll-margin-top: var(--nav-height);
`;

const Category = styled.div`
  line-height: var(--category-line-height);
  font-size: var(--category-font);
  font-weight: bold;
`;

const Content = styled.div`

`;

const SessionDisplay = styled.div`
  min-height: var(--min-height);
  background-color: white;
  border-radius: var(--display-border-radius);
  border-color: black;
  padding: var(--display-padding);
  text-align: justify;
`;

const Title = styled.div`
  font-size: var(--title-font);
  text-align: left; 
  font-weight: bold;
`;

const Description = styled.code`
  font-size: var(--description-font);
  line-height: 1.2;
  text-align: justify;
  
`;
export default DescriptionBoard;
