import { styled } from 'styled-components';
import './App.css';
import { Component } from 'react';
import Map from './Map';
import EmbedMap from './EmbedMap';

class ScheduleBoard extends Component{
  render(props){
    return <Container id="Schedule">
      <Category>
        {this.props.language === 'en' ? 'Find me at' : ' '}
      </Category>
      <Content>
        <MapDisplay>
          <Title>
            {this.props.language === 'en' ? '105 Judd St. London' : ',   105'}
          </Title>
          <Map>

          </Map>
          {/* <EmbedMap /> */}
        </MapDisplay>
        <TimeTableDisplay>
          <Title>
          {this.props.language === 'en' ? 'Schedule' : ''}
          </Title>
          <div>
          <ul>
            <li>31st. May</li>
          </ul>
          <ul>
            <li></li>
          </ul>
          </div>
        </TimeTableDisplay>
      </Content>
    </Container>;
  }
}
export default ScheduleBoard;

const Container = styled.div`
  scroll-margin-top: var(--nav-height);
`;

const Category = styled.div`
  line-height: var(--category-line-height);
  font-size: var(--category-font);
  font-weight: bold;
`;

const Content = styled.div`

`;

const MapDisplay = styled.div`
  background-color: white;
  min-height: 25rem;
  display: inline-block;
  border-radius: var(--display-border-radius);
  padding: var(--display-padding);

  iframe{
  top: 0;left: 0;right: 0;bottom: 0;
  min-height: 20rem;
}
`;

const TimeTableDisplay = styled.div`
  background-color: white;
  min-height: var(--min-height);
  border-radius: var(--display-border-radius);
  padding: var(--display-padding);
`;

const Title = styled.div`
  font-size: var(--title-font);
  font-weight: bold;
  text-align: center;
`;