import { styled } from 'styled-components';
import './App.css';
import { Component } from 'react';

class BehindTheSceneBoard extends Component{
  render(){
    return <Container id='BehindTheScene'>
      <Category>
        {this.props.language === 'en' ? 'Behind the scenes' : ' '}
        </Category>
        <Content>
          <GallaryDisplay>
            <div><img src='Behind_01.jpg' /></div>
            <div><img src='Behind_02.jpg' /></div>
            <div><img src='Behind_03.jpg' /></div>
          </GallaryDisplay>
        </Content>
    </Container>;
  }
}

export default BehindTheSceneBoard;

const Container = styled.div`
  scroll-margin-top: var(--nav-height);
`;

const Category = styled.div`
  line-height: var(--category-line-height);
  font-size: var(--category-font);
  font-weight: bold;
`;

const Content = styled.div`

`;

const GallaryDisplay = styled.div`
  background-color: white;
  min-height: calc(var(--min-height));
  border-radius: var(--display-border-radius);
  padding: var(--display-padding);

  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;

  div {
    /* justify-content: space-between; */
    width: 49%;
    background-size: cover;
    background-position: center top;
  }

  div:nth-child(1) {
    width: 100%;
  }

  /* div:nth-child(1) {
    background-image: url('Behind_01.jpg');
  }
  div:nth-child(2) {
    background-image: url('Behind_02.jpg');
  } */

`;
