import { styled } from 'styled-components';
import './App.css';
import './common.css';
import { Component } from 'react';

class MenuBoard extends Component{
    render(props){
        return <Container id="Menu">
          <Category>
            {this.props.language === 'en' ? 'Menu' : ''}
          </Category>
          <Content>
            <MenuDisplay>
                
            </MenuDisplay>
          </Content>
        </Container>;
    }
};

export default MenuBoard;

const Container = styled.div`
  scroll-margin-top: var(--nav-height);
`;

const Category = styled.div`
  line-height: var(--category-line-height);
  font-size: var(--category-font);
  font-weight: bold;
`;

const Content = styled.div`

`;

const MenuDisplay = styled.div`
  background-color: white;
  min-height: var(--min-height);
  border-radius: var(--display-border-radius);
  padding: var(--display-padding);
`;
